@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Inter', sans-serif;position:relative}.light{font-weight:200}.regular{font-weight:400}.bold{font-weight:700}.btn{border:0;border-radius:0;display:block;padding:0.5rem 1.5rem;cursor:pointer;margin:0.5rem;cursor:pointer;font-family:'Rubik', sans-serif;background-color:transparent;transition:0.3s}.btn a{color:white}.btn--orange{border:1px #F2994A solid;background-color:#F2994A}.btn--orange:hover,.btn--orange:focus{border:1px #fff solid;background-color:rgba(132,118,170,0.3)}.btn--outline{border:1px #F2994A solid;color:#F2994A}.btn--outline:hover,.btn--outline:focus{border:1px #fff solid;background-color:rgba(132,118,170,0.3);color:#fff}a{text-decoration:none;cursor:pointer}.not-published{cursor:no-drop}.not-published__overlay{position:absolute;top:0;right:0;width:100%;height:100%;font-size:21px;display:flex;justify-content:center;align-items:center;color:white;text-align:center;background-color:transparent;transition:background-color 0.5s}.not-published__overlay:hover,.not-published__overlay:focus{background-color:rgba(0,0,0,0.7)}.app{overflow:hidden;width:100%;height:100vh;overflow-y:scroll;background:linear-gradient(to top right, #3B1D60 -70%, #040D3E 25%)}@media (min-width: 1024px){.app{display:flex;flex-direction:column;align-items:center;justify-content:center}}.app__bg{width:100%;position:absolute;top:15%;right:0;z-index:1}.app__intro{padding:1rem;width:100%;display:flex;flex-direction:column;align-items:center;cursor:default}@media (min-width: 1024px){.app__intro{width:80%}}@media (min-width: 768px){.app__intro{flex-direction:row;justify-content:space-between}}.app__intro .intro{display:flex;flex-direction:column}.app__intro .intro__title{color:#99EFD0}.app__intro .intro__subtitle{color:#8476AA}.app__intro .btns{display:flex;z-index:2}.app__projects{padding:1rem;position:relative;display:flex;flex-direction:column;align-items:center}.app__projects__title{color:#99EFD0;z-index:2;cursor:default}.app__projects__collection{display:flex;flex-wrap:wrap;width:95%;z-index:3}@media (min-width: 1024px){.app__projects__collection{width:85%;justify-content:center}}.app__projects__collection .collection__card{padding:2rem;margin:1rem;display:block;transition:background-color 0.5s;text-align:left}@media (min-width: 1024px){.app__projects__collection .collection__card{width:calc(90% / 3)}}.app__projects__collection .collection__card .card__title{color:#99EFD0}.app__projects__collection .collection__card .card__details{color:#8476AA}.app__projects__collection .collection__card:hover,.app__projects__collection .collection__card:focus{background-color:rgba(132,118,170,0.3)}.app__projects__collection .collection__card:hover .card__details,.app__projects__collection .collection__card:focus .card__details{color:#fff}

